
























export default {
	data() {
		return {
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: uni.getStorageSync('isCanUse') || true //默认为true
		};
	},
	methods: {
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo(detail) {
			console.log("phone",detail);
			wx
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
				   console.log("phon",infoRes)
					_this.nickName = infoRes.userInfo.nickName; //昵称
					_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
						
					try {
						uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
						_this.updateUserInfo();
					} catch (e) {}
				},
				fail(res) {}
			});
		},
  

  
     
		//登录
		login() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});

			// 1.wx获取登录用户code
			uni.login({
				// provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes)
					let code = loginRes.code;

					if (!_this.isCanUse) {
						//非第一次授权获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								console.log(infoRes)
								//获取用户信息后向调用信息更新方法
								let nickName = infoRes.userInfo.nickName; //昵称
								let avatarUrl = infoRes.userInfo.avatarUrl; //头像
								
								_this.updateUserInfo(); //调用更新信息方法
							}
						});
					}

					//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					uni.request({
						url: "http://shanpei.wsstreet.net/getOpenid",
						data: {
							code: code,
						},
						method: 'POST',

						success: (res) => {
							//console.log(res)
							_this.OpenId=res.data.data.openid;
							//openId、或SessionKdy存储//隐藏loading
							uni.hideLoading();
						}
					});
				},
			});
		},
		//向后台更新信息
		updateUserInfo() {
		    let _this = this;
		    uni.request({
		        url:"http://shanpei.wsstreet.net/reg" ,//服务器端地址
		        data: {
		            openid:_this.OpenId,
					headimg:_this.avatarUrl,
					nickname:_this.nickName
		        },
		        method: 'POST',
		       
		        success: (res) => {
					//console.log( "22",res)
		            if (res.data.code==1) {
						var info=JSON.stringify(res.data.data)
						uni.setStorage({
							key:"userinfo",
							data:info,
							success:(res)=>{
								console.log("用户信息保存成功")
							}
						})
		                uni.navigateTo({
		                	
		                
		                    url: '/pages/getphone/getphone'
		                });
		            }
		        }
		       
		    });
		}
	},
	onLoad() { //默认加载
		this.login();
	}
}
